.course-section-container {
  display: flex;
  flex-direction: column;
  height: 100%;

  .course-head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 1rem 2rem;
    border-bottom: 0.4px solid rgb(194, 194, 194);

    a {
      color: rgb(48, 48, 48);
      background-color: white;
      border: 0.25px solid rgb(194, 194, 194);
      padding: 0 2rem;
      box-shadow: 2px 2px 2.5px 1px#dfdfdf;
      font-weight: 500;
      &:hover {
        background-color: rgb(235, 235, 235);
      }
    }
  }

  h5 {
    font-weight: 400;
    margin-top: 0.7rem;
    padding-left: 1rem;
  }

  .course-container {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem 2rem;
    row-gap: 1rem;
    column-gap: 1rem;

    .course {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      background-color: white;
      width: 270px;
      height: 260px;
      padding: 1rem;
      box-shadow: 2px 2px 2.5px 1px#dfdfdf;
      &:hover {
        background-color: rgb(230, 230, 230);
      }

      img {
        width: 80px;
        height: 80px;
        border-radius: 5px;
      }

      span {
        color: rgb(48, 48, 48);
      }

      hr {
        width: 80%;
        border: none;
        border-top: 0.1px solid rgb(231, 231, 231);
      }

      button {
        background-color: rgb(106, 108, 171);
        border-radius: 2px;
        border: none;
        color: white;
        padding: 0.2rem 2rem;
        &:hover {
          background-color: rgb(70, 71, 117);
        }
      }
    }
  }

  .data-not-found {
    display: flex;
    flex-direction: column;
    flex: 1;
    justify-content: center;
    align-items: center;
    svg {
      width: 32em;
      height: 32em;
      padding: 8em;
      background-color: white;
      clip-path: circle(40%);
    }
    span {
      padding-top: 1rem;
    }
  }
}
