@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&display=swap");

.register-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 0.3s;

  h1 {
    padding: 2rem 0;
  }

  #classaid {
    font-family: "Inconsolata", monospace;
  }

  .error {
    width: 60%;
    max-width: 400px;
    color: rgb(255, 115, 115);
    display: flex;
    justify-content: center;
  }

  .login-page {
    display: flex;
    span {
      padding: 0 0.5rem;
    }

    a {
      color: #1074e7;
      text-decoration: none;
    }
  }
}

.form {
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem 2rem 2rem;
  border-radius: 5px;
  width: 60%;
  max-width: 400px;

  div {
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;

    span {
      font-weight: bold;
      padding: 0.5rem 0;
    }

    input {
      padding: 0.5rem 0.4rem;
      border: 1px solid rgb(190, 190, 190);
      border-radius: 4px;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) all 0.4s;
      &:focus {
        outline: none;
        border-color: rgb(156, 210, 255);
      }
    }

    select {
      padding: 0.5rem 0.4rem;
      option {
        padding: 0.5rem 0.4rem;
      }
    }

    button {
      background-color: #1074e7;
      border: 0.2px solid rgb(3, 102, 214);
      padding: 0.6rem 0;
      border-radius: 4px;
      color: white;
      font-weight: bold;

      &:hover {
        background-color: rgb(3, 102, 214);
      }

      & .clicked {
        background-color: rgb(3, 102, 214);
      }
    }
  }
  .login-button-group {
    margin-top: 2rem;
  }
}
