.login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 0.3s;

  h1 {
    padding: 2rem 0;
  }
}

.form-error {
  width: 60%;
  max-width: 400px;
  background-color: rgb(255, 203, 203);
  border: rgb(255, 180, 180) 0.5px solid;
  margin: 2rem 1rem;
  padding: 1rem;
  text-align: center;
  border-radius: 5px;
}

.login-form {
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(230, 230, 230);
  padding: 1rem 2rem 2rem 2rem;
  border-radius: 5px;
  width: 60%;
  max-width: 400px;
  background-color: rgb(246, 248, 250);

  div {
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;

    span {
      font-weight: bold;
      padding: 0.5rem 0;
    }

    input {
      padding: 0.5rem 0.4rem;
      border: 1px solid rgb(190, 190, 190);
      border-radius: 4px;
      transition: cubic-bezier(0.075, 0.82, 0.165, 1) all 0.4s;
      &:focus {
        outline: none;
        border-color: rgb(156, 210, 255);
      }
    }

    button {
      background-color: #2ea44f;
      border: 0.2px solid rgb(44, 151, 75);
      padding: 0.6rem 0;
      border-radius: 4px;
      color: white;
      font-weight: bold;

      &:hover {
        background-color: rgb(44, 151, 75);
      }

      & .clicked {
        background-color: rgb(44, 151, 75);
      }
    }
  }
}

.signup-container {
  margin: 1rem 0;
  padding: 1rem;
  border: 1px solid #d1d5da;
  width: 60%;
  max-width: 400px;
  display: flex;
  flex-wrap: wrap;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  a {
    color: #0366d6;
    text-decoration: none;
  }
  span {
    padding: 0 0.5rem;
  }
}
