.class-detail-container {
  display: flex;
  flex-direction: row;
  height: 100%;
  .left-section {
    width: 360px;
    background-color: white;
    height: 100%;
    padding: 1rem;

    .back-link {
      display: flex;
      justify-content: flex-start;
      a {
        display: flex;
        align-items: center;
        font-size: 0.9rem;
        margin-left: -0.5rem;
        color: rgb(143, 143, 143);
        &:hover {
          color: rgb(106, 108, 171);
        }
      }
    }
    img {
      padding: 1rem 0;
    }

    span {
      font-weight: 600;
      font-size: 1.1rem;
    }

    ul {
      margin-top: 2rem;
      list-style: none;
      li {
        margin: 0 -1rem;
        padding: 0 1rem;
        &:hover {
          background-color: rgb(235, 235, 235);
          cursor: pointer;
        }
      }
    }
  }
}
