.right-section {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  .rigth-section-head {
    border-bottom: 1px solid rgb(209, 209, 209);
    h2 {
      padding: 0 1rem;
    }
    height: 3.5rem;
    display: flex;
    align-items: center;
    ul {
      list-style: none;
      display: flex;
      padding-left: 1rem;
      gap: 0 0.7rem;
      li {
        cursor: pointer;
        z-index: 4;
        padding: calc(0.8rem + 4px) 0 calc(0.8rem - 4px) 0;
        border-bottom: 4px solid transparent;
        &:hover {
          border-bottom: 4px solid rgb(150, 150, 153);
        }
      }

      .selected {
        border-bottom: 4px solid #464775;
        &:hover {
          border-bottom: 4px solid #464775;
        }
      }
    }
    .container-two-side {
      display: flex;
      width: 100%;
      justify-content: space-between;
      padding: 0 1rem;
      div {
        display: flex;
        align-items: center;
      }
      button {
        background-color: rgb(106, 108, 171);
        border-radius: 2px;
        border: none;
        color: white;
        padding: 0.2rem 1rem;
        box-shadow: 2px 2px 2.5px 1px#dfdfdf;
        &:hover {
          background-color: rgb(70, 71, 117);
        }
      }
    }
  }
  .right-section-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    .Chatx {
      display: flex;
      flex-direction: column;
      height: 100%;
      max-height: 100%;
      .button-fetch {
        z-index: 2;
        position: absolute;
        padding: 0.3rem 0;
        display: flex;
        justify-content: center;
        width: 100%;
        cursor: pointer;
        &:hover {
          cursor: pointer;
        }
      }
      .scroll {
        position: static;
        overflow-y: auto;
        height: calc(80vh);
        padding: 2rem 1rem 0 1rem;
        .messageBox {
          background: #464775;
          padding: 5px 20px;
          color: white;
          display: inline-block;
          max-width: 80%;
        }

        .messageText {
          width: 100%;
          letter-spacing: 0;
          float: left;
          font-size: 1.1em;
          word-wrap: break-word;
        }

        .messageText img {
          vertical-align: middle;
        }

        .messageContainer {
          display: flex;
          justify-content: flex-end;
          padding: 0 5%;
          margin-top: 3px;
        }

        .sentText {
          display: flex;
          align-items: center;
          font-family: Helvetica;
          color: #2b2b2b;
          letter-spacing: 0.3px;
        }

        .pl-10 {
          padding-left: 10px;
        }

        .pr-10 {
          padding-right: 10px;
        }

        .justifyStart {
          justify-content: flex-start;
        }

        .justifyEnd {
          justify-content: flex-end;
        }

        .colorWhite {
          color: white;
        }
      }
      .chat-input {
        padding: 1rem 0;
        width: 100%;
        display: flex;
        justify-content: center;
        input {
          border-radius: 2px;
          padding: 0.5rem 0.75rem;
          border: 2px solid transparent;
          width: 60%;
          &:focus {
            outline: none;
            border-bottom: 2px solid #464775;
          }
        }
        .send-button {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 0 1rem;
          color: white;
          background: whitesmoke;
          &:hover {
            color: #464775;
          }
        }
      }
    }
    .file-upload-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      .file-upload-head {
        border-bottom: 1px solid rgb(209, 209, 209);
        display: flex;
        flex-wrap: wrap;
        padding: 0.7rem 1rem;
        align-items: center;
        div {
          flex: 1;
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 0 2rem;
          .file-upload-input {
            padding: 0.25rem 0.5rem;
          }
          .file-name-input {
            border-radius: 2px;
            padding: 0.25rem 0.5rem;
            border: 2px solid transparent;
            flex: 1;
            &:focus {
              outline: none;
              border-bottom: 2px solid #464775;
            }
          }
        }
        button {
          color: rgb(48, 48, 48);
          background-color: white;
          border: 0.25px solid rgb(194, 194, 194);
          padding: 0.2rem 2rem;
          margin: 0 1rem;
          box-shadow: 2px 2px 2.5px 1px#dfdfdf;
          font-weight: 600;
          &:hover {
            background-color: rgb(235, 235, 235);
          }
        }
      }
      .file-upload-content {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 1.5rem 0;
        .row {
          display: flex;
          padding: 0.5rem 1.25rem;
          border-bottom: 1px solid rgb(219, 219, 219);
          div {
            flex: 1;
          }
          div:first-child {
            flex: 2;
          }
        }
      }
    }
    .user-container {
      flex: 1;
      display: flex;
      flex-direction: column;
      padding: 1rem 2rem;
      .row {
        padding: 0.2rem 0.5rem;
        margin: 0.25rem 0;
        display: flex;
        background: white;
        li {
          button {
            color: rgb(48, 48, 48);
            background-color: white;
            border: 0.25px solid rgb(194, 194, 194);
            padding: 0 2rem;
            box-shadow: 2px 2px 2.5px 1px#b6b6b6;
            font-weight: 500;
            &:hover {
              background-color: rgb(235, 235, 235);
            }
          }
          padding: 0.5rem 0;
          flex: 1;
          list-style: none;
        }
      }
      .head {
        background: none;
      }
    }
    .quiz-container {
      flex: 1;
      overflow-y: auto;
      .create-quiz-container {
        padding: 1rem;
        form {
          padding: 0.5rem 1rem;
          width: 100%;
          div {
            display: flex;
            width: 100%;
            align-content: flex-start;
            padding: 0.5rem 0;
            label {
              min-width: 5.5rem;
              padding: 0.25rem 1rem 0.25rem 0;
              font-size: 1.1rem;
            }
            input {
              border-radius: 2px;
              padding: 0.25rem 0.5rem;
              border: 2px solid transparent;
              flex: 1;
              &:focus {
                outline: none;
                border-bottom: 2px solid #464775;
              }
            }
            select {
              flex: 1;
              text-align: center;
            }
          }
          button {
            background-color: rgb(106, 108, 171);
            border-radius: 2px;
            border: none;
            width: 100%;
            color: white;
            padding: 0.2rem 2rem;
            &:hover {
              background-color: rgb(70, 71, 117);
            }
          }
          .half {
            width: 50%;
            padding: 0.5rem;
          }
        }
      }
      .quiz-list {
        flex: 1;
        display: flex;
        flex-direction: column;
        padding: 1rem;
        // background: rgb(106, 108, 171);
        overflow: scroll;
        .row {
          overflow: scroll;
          display: flex;
          flex-wrap: wrap;
          .card {
            display: flex;
            flex-direction: column;
            background-color: white;
            padding: 1rem;
            margin: 0.7rem;
            box-shadow: 2px 2px 2.5px 1px #dfdfdf;
            div {
              display: flex;
              justify-content: space-around;
              margin-top: 0.25rem;
              button {
                background-color: rgb(106, 108, 171);
                border-radius: 2px;
                border: none;
                color: white;
                padding: 0.2rem 0.25rem;
                margin: 0 0.25rem;
                &:hover {
                  background-color: rgb(70, 71, 117);
                }
              }
            }
          }
        }
      }
    }
  }
}
