.home {
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;

  .float-window {
    display: flex;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(25, 25, 25, 0.3);
    justify-content: center;
    align-items: center;
    z-index: 10;
    .float-window-container {
      background-color: white;
      padding: 1.5rem 1.5rem;
      width: 600px;
      height: 450px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      div button {
        color: rgb(48, 48, 48);
        background-color: white;
        border: 0.25px solid rgb(194, 194, 194);
        padding: 0.2rem 2rem;
        box-shadow: 2px 2px 2.5px 1px#dfdfdf;
        font-weight: 600;
        &:hover {
          background-color: rgb(235, 235, 235);
        }
      }

      div .file-name-input {
        border-radius: 2px;
        padding: 0.25rem 0.5rem;
        border: 2px solid transparent;
        width: 100%;
        &:focus {
          outline: none;
          border-bottom: 2px solid #464775;
        }
        margin-top: 0.7rem;
      }
      .quiz-box {
        display: flex;
        flex: 1;
        flex-direction: column;
        width: 100%;
        div {
          flex: 1;
          text-align: center;
        }
        ul {
          display: flex;
          list-style: none;
          flex-direction: column;
          width: 100%;
          li {
            flex: 1;
            padding: 0.25rem 0;
            button {
              width: 100%;
            }
          }
        }
      }
      .quiz-list {
        padding: 1rem;
        list-style: none;
        li {
          display: flex;
          justify-content: space-between;

          button {
            color: rgb(48, 48, 48);
            background-color: white;
            border: 0.25px solid rgb(194, 194, 194);
            padding: 0.2rem 2rem;
            box-shadow: 2px 2px 2.5px 1px#dfdfdf;
            font-weight: 600;
            &:hover {
              background-color: rgb(235, 235, 235);
            }
          }
        }
      }

      .svg-container {
        flex: 1;
        padding: 3rem 25%;
      }

      .user-container {
        display: flex;
        flex-direction: column;
        width: 100%;
        ul {
          padding: 1rem;
          list-style: none;
          li {
            display: flex;
            justify-content: space-between;
          }
        }
        form {
          display: flex;
          flex-direction: column;
          .error {
            color: red;
          }
          svg {
            width: 5em;
            height: 5em;
          }
          div {
            display: flex;
            flex-direction: column;
            padding: 0.5rem 0;
            label {
              font-size: 0.8rem;
            }
            input {
              background: rgb(235, 235, 235);
              border: none;
              padding: 0.5rem;
            }
          }
          button {
            margin-top: 1rem;
            background-color: rgb(106, 108, 171);
            border-radius: 2px;
            border: none;
            color: white;
            padding: 0.2rem 2rem;
            &:hover {
              background-color: rgb(70, 71, 117);
            }
          }
        }
      }
      .button-container {
        display: flex;
        justify-content: flex-end;
      }

      .two-button {
        justify-content: space-between;
        .secondary-btn {
          background-color: #6a6cab;
          color: whitesmoke;
          &:hover {
            background-color: rgb(70, 71, 117);
          }
        }
      }

      .hidden-input {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        margin-top: 1rem;
        padding: 0 0.5rem;
        h5 {
          position: absolute;
        }
        span {
          position: absolute;
        }
        input {
          width: 100%;
          cursor: pointer;
          opacity: 0;
        }
      }
    }
  }

  .nav {
    display: flex;
    flex: 0;
    padding: 0.2rem 1rem;
    justify-content: space-between;
    align-items: center;
    background-color: #464775;
    font-size: large;
    #logo {
      font-size: 1.6rem;
      color: whitesmoke;
    }

    .user-control {
      position: relative;
      flex-direction: column;
      z-index: 5;

      .user-profile-picture {
        display: flex;
        justify-content: flex-end;
        padding: 0.25rem 0;
        cursor: pointer;
        img {
          width: 3rem;
          height: 3rem;
          border-radius: 50%;
        }

        &:hover {
          filter: opacity(0.8);
        }
      }
      .user-menu {
        display: none;
        transition: all cubic-bezier(0.39, 0.575, 0.565, 1);
        margin-top: 0.2rem;
        box-shadow: 2px 4px 3px 1px #dfdfdf;
        background: white;
        ul li {
          list-style: none;
          padding: 0.5rem 1.25rem;
          margin: 0.6rem 0;

          button {
            border: none;
            background-color: transparent;
          }
          &:hover {
            background: whitesmoke;
          }
        }

        ul hr {
          border: 1px solid whitesmoke;
        }
        .user-detail {
          display: flex;
          img {
            width: 4rem;
            height: 4rem;
            border-radius: 50%;
          }
          div {
            padding-left: 0.5rem;
          }
        }
      }
      .open {
        display: block;
        position: absolute;
        transform: translateX(calc(-100% + 50px));
      }
    }
  }

  .bottom {
    display: flex;
    flex: 1;
    .side-nav {
      background-color: #33344a;
      width: 70px;
      color: #eaeaed;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      font-size: 0.8rem;
      div {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        position: relative;
        padding: 0.5rem 0;
        background-color: #3c3e60;
        &::after {
          content: "";
          border: #e2e2f6 2px solid;
          position: absolute;
          height: 3.6rem;
          transform: translateX(-30px);
        }
      }
    }

    .content {
      width: 100%;
      flex: 1;
      overflow-x: hidden;
      background-color: whitesmoke;
    }
  }
}
