.all-course-section-container {
  display: flex;
  flex-direction: column;

  .back-link {
    display: flex;
    justify-content: flex-start;
    padding: 1rem 2rem 0 2rem;
    a {
      display: flex;
      align-items: center;
      font-size: 1.2rem;
      margin-left: -0.5rem;
      color: rgb(143, 143, 143);
      &:hover {
        color: rgb(106, 108, 171);
      }
    }
  }
  .course-head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0.5rem 2rem;

    a {
      color: rgb(48, 48, 48);
      background-color: white;
      border: 0.25px solid rgb(194, 194, 194);
      padding: 0 2rem;
      box-shadow: 2px 2px 2.5px 1px#dfdfdf;
      font-weight: 500;
      &:hover {
        background-color: rgb(235, 235, 235);
      }
    }
  }

  .course-container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 2rem;
    row-gap: 1rem;
    column-gap: 1rem;

    .course {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      background-color: white;
      width: 270px;
      height: 260px;
      padding: 1rem;
      &:hover {
        background-color: rgb(230, 230, 230);
      }

      img {
        width: 80px;
        height: 80px;
        border-radius: 5px;
      }

      hr {
        width: 80%;
        border: none;
        border-top: 0.1px solid rgb(211, 211, 211);
      }

      button {
        color: rgb(48, 48, 48);
        background-color: white;
        border: 0.25px solid rgb(194, 194, 194);
        padding: 0 2rem;
        box-shadow: 2px 2px 2.5px 1px#b6b6b6;
        font-weight: 500;
        &:hover {
          background-color: rgb(235, 235, 235);
        }
      }

      .create-course-button {
        background-color: rgb(106, 108, 171);
        border-radius: 2px;
        border: none;
        color: white;
        padding: 0.2rem 2rem;
        &:hover {
          background-color: rgb(70, 71, 117);
        }
      }
    }

    .author-button {
      color: rgb(48, 48, 48);
      background-color: white;
      border: 0.25px solid rgb(194, 194, 194);
      padding: 0 2rem;
      box-shadow: 2px 2px 2.5px 1px#b6b6b6;
      font-size: 0.9rem;
      font-weight: 500;
      &:hover {
        background-color: rgb(235, 235, 235);
      }
    }
  }
}
